* {
    box-sizing: border-box;
}

html,
body,
#root {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
}

body {
    background-color: #f1f4f8;
    position: fixed;
}

.MuiButton-root {
    padding-left: 100px;
}

.header {
    position: absolute;
    top: 5px;
    left: 0;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    z-index: 1;
}
.header_items {
    padding-left: 10px;
}
.info_container {
    position: absolute;
    right: 15px;
    bottom: 15px;
    width: 350px;
    max-height: 100%;
    max-width: 90%;
    display: flex;
    flex-direction: column;
    font-size: 0.9em;
    padding: 25px;
    border-radius: 5px;
    background: linear-gradient(0, #1e88e5, #000 240%);
    color: #fff;
    box-shadow: 2px 10px 30px rgba(0, 0, 0, 0.4);
    opacity: 0.4;
    z-index: 10000;
    -webkit-clip-path: circle(30px at calc(100% - 30px) calc(100% - 30px));
    clip-path: circle(30px at calc(100% - 30px) calc(100% - 30px));
    transition: all 0.4s ease-in-out;
}
.info_container:hover {
    bottom: 20px;
}
.info_container.centered,
.info_container:hover {
    -webkit-clip-path: circle(200% at calc(100% - 30px) calc(100% - 30px));
    clip-path: circle(200% at calc(100% - 30px) calc(100% - 30px));
    opacity: 1;
}
.info-container.centered {
    max-height: 90%;
    min-height: 50%;
    width: 600px;
    max-width: 95%;
    font-size: 1em;
    text-align: center;
    right: 50%;
    bottom: 50%;
    transform: translate(50%, 50%);
}
.info-container.alert {
    -webkit-animation: alert 1s ease-in-out;
    animation: alert 1s ease-in-out;
}
.settings_container {
    position: absolute;
    right: 10px;
    top: 50px; //controls where the circle is
    width: 350px;
    max-height: 100%;
    max-width: 90%;
    display: flex;
    flex-direction: column;
    font-size: 0.9em;
    padding: 25px;
    border-radius: 5px;
    background: linear-gradient(0, #1e88e5, #000 240%);
    color: #fff;
    box-shadow: 2px 10px 30px rgba(0, 0, 0, 0.4);
    z-index: 100;
    -webkit-clip-path: circle(30px at 90% 5%);
    clip-path: circle(30px at 90% 7%);
    transition: all 0.4s ease-in-out;
}

.settings_container:hover {
    top: 55px;
    position: absolute;
}

.settings_container.centered,
.settings_container:hover {
    -webkit-clip-path: circle(200% at 0% 0%);

    clip-path: circle(200% at 0% 0%);
}
.settings-container.centered {
    max-height: 90%;
    min-height: 50%;
    width: 600px;
    max-width: 95%;
    font-size: 1em;
    text-align: center;
    right: 50%;
    bottom: 50%;
    transform: translate(50%, 50%);
}

.settings-container.alert {
    -webkit-animation: alert 1s ease-in-out;
    animation: alert 1s ease-in-out;
}
